<template>
	<view class="db-longsegment">
		<scroll-view class="scroll" :style="{'white-space':'nowrap'}" scroll-x="true">
			<view @click="clickmsg(item.id,index)" v-for="(item,index) in titlelist" 
			class="cellbg" :key='item.id'>
				<view :class="{'default':true,'select':index === currentselectindex}">
					<view class="title">{{item.name}}</view>
					<view class="line"></view>
				</view>
			</view>

		</scroll-view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				currentselectindex: 0,
			}
		},

		props: {
			titlelist: {
				type: Array,
				default: []
			}
		},

		methods: {
			clickmsg(id, index) {

				this.currentselectindex = index;
				this.$emit('clickmsg', id);
			}
		},
		mounted() {

		}
	}
</script>

<style lang="scss">
	::-webkit-scrollbar {
		width: 0;
		height: 0 !important
	}

	.db-longsegment {
		::-webkit-scrollbar {
			width: 0;
			height: 0 !important
		}

		.scroll {
			height: 40px;
			.cellbg {
				display: inline-block;
				height: 40px;
				width: 21vw;

				.default {
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: space-around;
					height: 40px;
					width: 21vw;

					.line {
						height: 2px;
						width: 30px;
						border-radius: 1px;
						background-color: transparent;
					}

					.title {
						color: #999999;
						font-size: 16px;
					}
				}

				.select {
					@extend .default;
					.line {
						height: 2px;
						width: 30px;
						border-radius: 1px;
						background-color: #000000;
					}

					.title {
						color: #000000;
						font-size: 20px;
					}
				}
			}
		}

	}
</style>
